import * as types from '../constants/ActionTypes';
// const url ="https://gw.hellofresh.com/api/recipes/search?country=us&locale=en-US&limit=10&cuisine=italian&order=-rating&skip=0";
// const token = "eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJleHAiOjE1MzEzMjg4NzksImp0aSI6ImQ3ZjVkNmFiLTViNmEtNGQ1Mi05MGY1LWQ0MDlmMTZiMTg3YiIsImlhdCI6MTUyODY5OTEzNiwiaXNzIjoiaGVsbG9mcmVzaC1kZXYtdGVzdCJ9.Yoo2Z7yiOdKDNPLMTcwgTsC3o1nG3_Qh0llMF-jipXE";
const headers = new Headers();
const method = "GET"
headers.append("Accept", "application/json");


export const recipesRequest = () => {
  return {
    type: types.RECIPES_REQUEST
  };
}

export const recipesReceive = (json) => {
  return {
    type: types.RECIPES_RECEIVE,
    gets: json,
    receivedAt: Date.now()
  }
}

const fetchRecipes = (token ,cuisines, pageSize, skip) => dispatch => {
  dispatch(recipesRequest())
  headers.append("Authorization", `Bearer ${token}`);

  const options = {
    method,
    headers
  };
  const url = "https://gw.hellofresh.com/api/recipes/search?country=us&locale=en-US&limit=" + pageSize + "&cuisine=" + cuisines + "&order=-rating&skip=" + skip;
  return fetch(url, options)
    .then(response => response.json())
    .then(json => dispatch(recipesReceive(json)))
}

export const fetchRecipesIfNeeded = (token, cuisines, pageSize, skip) => (dispatch, getState) => {

  return dispatch(fetchRecipes(token, cuisines, pageSize, skip))

}



