import React from "react";
import styled from 'styled-components'
import lemonFlatSVG from './../../assets/logo/lemon-flat.svg'

const Container = styled.div`
position:absolute;
top:0;
background-color: rgba(255,255,255,0.3);
width: 100%;
height: 100%;
display:flex;
align-items: center;

/* @keyframes fadein {
  from {
    background-color: rgba(255,255,255,0.0);
  }
  to {
    background-color: rgba(255,255,255,1.0);
  }
}

-webkit-animation: fadein  10s linear 1;
  -moz-animation: fadein  10s linear 1;
  -ms-animation: fadein  10s linear 1;
  -o-animation: fadein  10s linear 1;
  animation: fadein  10s linear 1; */
`;

const InerContainer = styled.div`
margin: auto;
width: 50px;
`;
const LemonFlat = styled.img`
width:50px;
margin: auto;
flex-direction: column;
flex-wrap: wrap;

@keyframes rotating {
  from {
    -ms-transform: rotate(0deg);
    -moz-transform: rotate(0deg);
    -webkit-transform: rotate(0deg);
    -o-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  to {
    -ms-transform: rotate(360deg);
    -moz-transform: rotate(360deg);
    -webkit-transform: rotate(360deg);
    -o-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

-webkit-animation: rotating 2s linear infinite;
  -moz-animation: rotating 2s linear infinite;
  -ms-animation: rotating 2s linear infinite;
  -o-animation: rotating 2s linear infinite;
  animation: rotating 2s linear infinite;

`;

const Text = styled.div`
width:77px;
font-size: 13px;
font-weight: bold;
align-items: center;
color: white;
text-align: center;
`;

class SmalLoader extends React.Component {
    render(){
        return(
        <Container>
            <InerContainer>
            <LemonFlat src={lemonFlatSVG}/>
            <Text>Loading . . .</Text>
            </InerContainer>
        </Container>
        )
    }
}


export default SmalLoader


