import * as types from '../constants/ActionTypes';

const url = "https://gw.hellofresh.com/api/cuisines"
//const token = "eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJleHAiOjE1MzEzMjg4NzksImp0aSI6ImQ3ZjVkNmFiLTViNmEtNGQ1Mi05MGY1LWQ0MDlmMTZiMTg3YiIsImlhdCI6MTUyODY5OTEzNiwiaXNzIjoiaGVsbG9mcmVzaC1kZXYtdGVzdCJ9.Yoo2Z7yiOdKDNPLMTcwgTsC3o1nG3_Qh0llMF-jipXE";
const headers = new Headers();
const method = "GET"
headers.append("Accept", "application/json");


export const cuisinesRequest = () => {
  return {
    type: types.CUISINES_REQUEST
  };
}

export const cuisinesReceive = (json) => {
  return {
    type: types.CUISINES_RECEIVE,
    gets: json,
    receivedAt: Date.now()
  }
}

const fetchCuisines = (token) => dispatch => {
  dispatch(cuisinesRequest())
  headers.append("Authorization", `Bearer ${token}`);

  const options = {
    method,
    headers
  };
  return fetch(url, options)
    .then(response => response.json())
    .then(json => dispatch(cuisinesReceive(json)))
}

function shoudeFetchCuisines(state) {
  // return true
  const gets = state.cuisines.lastUpdated
  if (!gets) {
    return true
  } else if (gets.isFetching) {
    return false
  } else {
    return gets.didInvalidate
  }
}

export const fetchCuisinesIfNeeded = (token) => (dispatch, getState) => {
  if (shoudeFetchCuisines(getState())) {
    return dispatch(fetchCuisines(token))
  }
}



