import React from "react"
import styled, {css} from 'styled-components'
import woodBackground from './../../assets/image/wood-background.jpg'
import hellofreshLogoMonoCoromSVG from './../../assets/logo/hellofresh-logo-mono-corom.svg'

const OuterContainer = styled.div`
height: 100%;
width: 100%;
background: #ebf8ea;
background-image: url(${woodBackground});
position: absolute;
`;

const Footer = styled.div`
background-color: rgba(98, 161, 69,1);
font-size:15px;
width:100%;
position:absolute;
bottom: 0;
color:white;
padding: 2px 0;
`;

const Header = styled.div`
height:100px;
width:100%;
background-color:rgba(145, 200, 79,1);
border-bottom:15px solid rgba(98, 161, 69,1);
@media (max-width: 900px) {
      ${ css`  
      display:none;
      ` }
    }
`;

const Logo = styled.img`
width:90px;
align-self: center;
`;

class TemplateOfRecipePage extends React.Component {
    render() {
            return (
                <OuterContainer>
                <Header>
                    <Logo src={hellofreshLogoMonoCoromSVG} />
                </Header>
                {this.props.children}
                <Footer>
                &nbsp; &nbsp; &nbsp; Hello FRESH © 
                    </Footer>
            </OuterContainer>
        )
    }
}

export default TemplateOfRecipePage


