import React from 'react'
import { connect } from 'react-redux'
import { fetchCuisinesIfNeeded, fetchRecipesIfNeeded } from './../../actions'
import {
    Redirect
  } from 'react-router-dom'
import TemplateOfRecipePage from './../../components/templateOfRecipePage'
import Cuisines from './../../components/cuisines'
import Recipes from './../../components/recipes'
import PageControl from './../../components/pageControl'

class RecepeReviewPage extends React.Component {
    state = {
        cuisine: "italian",
        pageSize: 20,
        skip: 0,
        recipes: {
            items: []
        }
    }
    componentDidMount() {
        const { dispatch, getToken } = this.props
        if(getToken.isFetched)
        dispatch(fetchCuisinesIfNeeded(getToken.response.access_token))
        this.setState({ recipes: { items: [] } })
        setTimeout(() => {
            if(getToken.isFetched)
            dispatch(fetchRecipesIfNeeded(getToken.response.access_token, this.state.cuisine, this.state.pageSize, this.state.skip))
        }, 100);
    }
    
    componentWillReceiveProps(nextProps) {
        this.setState({ recipes: nextProps.recipes })
    }
    
    reload = () => {
        const { dispatch, getToken } = this.props
        this.setState({ recipes: { items: [] } })
        setTimeout(() => {
            if(getToken.isFetched)
            dispatch(fetchRecipesIfNeeded(getToken.response.access_token, this.state.cuisine, this.state.pageSize, this.state.skip))
        }, 1);
    }

    render() {
        const { cuisines, getToken } = this.props
        const { recipes } = this.state
        if(getToken.isFetched){
        return (
            <div>
                <TemplateOfRecipePage>
                    <Cuisines value={cuisines.items} selected={(data) => {
                        this.setState({ cuisine: data })
                        this.reload()
                    }} />
                    <PageControl pageSize={this.state.pageSize} total={recipes.total} skip={recipes.skip} onChangeValue={(pageSize, skip) => {
                        this.setState({ pageSize })
                        this.setState({  skip })
                        this.reload()
                    }} />
                    <Recipes value={recipes.items} />
                </TemplateOfRecipePage>
            </div>
        )
    } else {
        return <Redirect to="/login" />
    }
    }
}


const mapStateToProps = state => {
    const { recipes, cuisines, getToken } = state
    return { recipes, cuisines, getToken }
}

export default connect(mapStateToProps)(RecepeReviewPage)


