import React from 'react'
import styled, {css} from 'styled-components'
import chevronRight from './../../assets/icon/Linearicons/chevron-right.svg'
import chevronLeft from './../../assets/icon/Linearicons/chevron-left.svg'
const Container = styled.div`
padding:10px;
display:flex;
direction:rtl;
`;

const PageSizeInput = styled.input`
background: rgba(0, 0, 0, 0.3);
border: 1px solid rgba(78,218,192,0);
width: 60px;
margin: 10px 0px 10px 0px;
padding: 10px 5px;
color: rgb(255, 255, 255);
font-weight: bold;
font-size: 24px;
`;

const Label = styled.span`
background: rgba(0, 0, 0, 0.1);
border: 1px solid rgba(78,218,192,0);
margin: 10px 0px 10px 10px;
padding: 10px 5px;
color: rgb(255, 255, 255);
font-weight: bold;
opacity:0;
font-size: 24px;
${props => props.show && css`
opacity:1;
`}
@media (max-width: 900px) {
      ${ css`  
      display:none;
      ` }
    }
`;

const BTN = styled.div`
background: rgba(0, 0, 0, 0.3);
border: 1px solid rgba(78,218,192,0);
transition: all 0.5s ease;
cursor:pointer;
width: 60px;
&:hover{
    background-color: rgba(0, 0, 0, 0.5);
}
background-repeat:no-repeat;
background-size: 60%;
background-position: 11px 7px;
${props => props.icon && css`
background-image: url(${props.icon});
`}
margin: 10px 0px 10px 10px;
padding: 10px 5px;
color: rgb(255, 255, 255);
font-weight: bold;
font-size: 24px;

`;


class PageControl extends React.Component {
    state={
        pageSize: this.props.pageSize,
        skip: 0
    }

    handlerChangePage = () => {
        this.setState({skip: this.props.skip + this.state.pageSize})
        this.props.onChangeValue(this.state.pageSize, this.props.skip + this.state.pageSize)
    }
    render(){
        return(
        <Container>
            <PageSizeInput value={this.state.pageSize} onChange={event => {
                this.setState({pageSize: event.target.value})
                         this.props.onChangeValue(event.target.value)
                    }} />
                    <Label show={true}>Page Size</Label>
                    <BTN icon={chevronRight} onClick= {this.handlerChangePage}/>
                    <BTN icon={chevronLeft} onClick= {this.handlerChangePage}/>
                    <Label show = {this.props.total} >Page {this.props.skip/this.state.pageSize +1+ ' / '+ ((this.props.total - this.props.skip)/this.state.pageSize)}  </Label>
                    <Label show = {this.props.total}>Total record  {this.props.total}  </Label>
        </Container>)
    }
}

export default PageControl


