import * as types from "../constants/ActionTypes";

export const recipes = (state = {}, action) => {
  switch (action.type) {
    case types.RECIPES_RECEIVE:
      return {
        ...state,
        isFetching: false,
        isFetched: true,
        didInvalidate: false,
        items: action.gets.items,
        lastUpdated: action.receivedAt,
        take: action.gets.take,
        skip: action.gets.skip,
        count: action.gets.count,
        total: action.gets.total,
      };
    case types.RECIPES_REQUEST:
      return {
        ...state,
        isFetching: true,
        isFetched: false,
        didInvalidate: false,
        take: null,
        skip: null,
        count: null,
        total: null,
      };
    default:
      return state;
  }
};





