import * as types from "../constants/ActionTypes";

export const getToken = (state = {}, action) => {
  switch (action.type) {
    case types.GET_TOKEN_RECEIVE:
    {
      localStorage.setItem("token", action.gets.access_token);
      return {
        ...state,
        isFetching: false,
        isFetched: true,
        didInvalidate: false,
        response: action.gets,
        lastUpdated: action.receivedAt
      };
    }
    case types.GET_TOKEN_REQUEST:
      return {
        ...state,
        isFetching: true,
        isFetched: false,
        didInvalidate: false,
      };
    default:
      return state;
  }
};





