import * as types from "../constants/ActionTypes";

export const cuisines = (state = {}, action) => {
  switch (action.type) {
    case types.CUISINES_RECEIVE:
      return {
        ...state,
        isFetching: false,
        isFetched: true,
        didInvalidate: false,
        items: action.gets.items,
        lastUpdated: action.receivedAt
      };
    case types.CUISINES_REQUEST:
      return {
        ...state,
        isFetching: true,
        isFetched: false,
        didInvalidate: false
      };
    default:
      return state;
  }
};





