import React from "react"
import PropTypes from 'prop-types'
import styled, {css} from 'styled-components'
import RecipeListElement from './../recipeListElement'
import Loader from './../smalLoader'

const Container = styled.div`
position: relative;
width: 100%;
height: -moz-fit-content;
padding: 40px, 0;
overflow: auto;
box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
background-color: rgba(255, 255, 255, 0.34);
@media (max-width: 900px) {
    ${ css`  
    padding: 5px 0;
    height: ${window.innerHeight-100 +'px'};
      ` }
    }
    @media (max-width: 900px) {
    ${ css`  
    padding: 5px 0;
      ` }
    }
@keyframes animat {
    from {
        opacity:0;
    }
    to {
        opacity:1;
    }
}
animation: animat 500ms ease ;
animation-fill-mode: forwards;
`;

const List = styled.div`
position: relative;
display:flex;
flex-direction: row;
flex-wrap: wrap;
width: -moz-fit-content;
height: ${window.innerHeight-250 +'px'};
`;


class Recipes extends React.Component {
    render() {
        const { value } = this.props
        if(value.length){
            return (
                <Container>
                    <List>
                        {value.map((item, id) =>
                            <RecipeListElement key={'RecipeListElement'+id} value={item} id={id} show={false} />
                        )}
                    </List>
                </Container>
        )
    } else{
        return (
            <React.Fragment><Loader/></React.Fragment>
        )
    }
    }
}

Recipes.propTypes = {
    value: PropTypes.array.isRequired,
}

Recipes.defaultProps = {
    value: [],
}

export default Recipes


