import React from 'react'
import {
    Redirect
  } from 'react-router-dom'
import Background from './../../assets/image/Background.jpg'
import hellofreshLogoSVG from './../../assets/logo/hellofresh-logo.svg'
import hellofreshLogoMonoCromeSVG from './../../assets/logo/hellofresh-logo-mono-corom.svg'
import user from './../../assets/icon/Linearicons/user.svg'
import lock from './../../assets/icon/Linearicons/lock.svg'
import styled, {css} from 'styled-components'
import Loader from './../smalLoader'

const ContainerPage = styled.div`
width: 100%;
height: ${window.innerHeight + 'px'}; 
background-repeat: no-repeat;
background-size: cover;
background-position: center;
display: flex;
align-items: center;
background: black;
overflow-x: hidden;
`;

const BackgroundPage = styled.div`
width: 100%;
position: fixed;
height: ${window.innerHeight + 'px'}; 
background-image: url(${Background});
background-repeat: no-repeat;
background-size: cover;
background-position: center;
display: flex;
align-items: center;
opacity: 1;
transition: all 0.5s ease;
${props => props.isFocused && css`
width: 1920px;
opacity: 0.7;
-ms-transform: scale(1.02, 1.02); /* IE 9 */
    -webkit-transform: scale(1.02, 1.02); /* Safari */
    transform: scale(1.02, 1.02);
`}
`;

const LoginPanel = styled.div`
width: 500px;
height: 80%;
overflow: hidden;
margin:auto;
position: relative;
transition: all 0.5s ease;
@media (max-width: 900px) {
    ${ css`  
    width:100%;
    height:100%;
      ` }
    }
${props => props.isFocused && css`
background-color: rgb(230, 179, 114);
`}
`;

const LoginPanelBg = styled.div`
background-image: url(${Background});
background-position: center center;
background-attachment: fixed;
height: 100%;
width: 100%;
position: absolute;
filter: blur(20px);
filter: blur(20px);
background-size: 100%;
`;

const ContentLogin = styled.div`
position: relative;
display: flex;
flex-direction: row;
flex-wrap: wrap;
height: 100%;
width: 100%;
background-color: rgba(97, 132, 0, 0.2);
`;

const HellofreshLogo = styled.img`
width: 100%;
margin-top: 133px;
height: 217px;
margin-bottom: -134px;
@media (max-width: 900px) {
    ${ css`  
    margin-top: 33px;
    margin-bottom: -14px;
      ` }
    }
`;

const InputContainer = styled.div`
width:100%;
position: relative;
`;

const LogoContainer = styled.div`
width:100%;

`;
const Input = styled.input`
background: rgba(255, 255, 255, 0.2);
border: 1px solid rgba(78,218,192,0);
width: 100%;
margin: 10px 0px 10px 0px;
padding: 10px 0px;
padding-left: 15%;
color: rgb(255, 255, 255);
font-weight: bold;
font-size: 24px;
`;

const Icon =styled.img`
width: 70px;
height: 73px;
position: absolute;
padding: 20px;
`;

const InputHolder = styled.div`
width:100%;
`;

const Btn = styled.div`
color: rgba(255,255,255,1);
background: rgb(145, 200, 79);
padding: 10px;
text-align: center;
margin: 10px 0px 0px 0px;
border: 1px solid rgba(78,218,192,0);
cursor: pointer;
width: 100%;
font-size: 24px;
transition: all 0.2s ease;
font-weight: bold;
text-shadow: 3px 3px rgb(98, 161, 69);
&:hover{
background: rgb(98, 161, 69);
color: rgba(255,255,255,1);
text-shadow: 3px 3px rgb(65, 106, 45);
    }
`;

const SidePanel = styled.div`
position: fixed;
top: 0px;
right: 0;
width: 500px;
height: 300px;
border-radius: 0px 0 0 30px;
background-color: rgb(230, 179, 114);
padding: 40px;
color: white;
box-shadow: 0 19px 38px rgba(0,0,0,0.30), 0 15px 12px rgba(0,0,0,0.22);
display: flex;
position: relative;
flex-direction: column;
flex-wrap: wrap;
transition: all 0.5s ease;
&:hover{
    width: 650px;
}
${props => props.isFocused && css`
width: 650px;
`}
@media (max-width: 900px) {
    ${ css`  
    display:none;
      ` }
    }
`;

const HellofreshLogoMonoCrome = styled.img`
height: 80px;
align-self: flex-end;
`;

const Title = styled.h1`
font-size: 26px;
`;

const Content = styled.div`
width: 450px;
font-size: 19px;
`;

const Link = styled.div`
color: white;
font-size: 20px;
font-weight: 400;
margin: auto;
align-self: center;
text-decoration: none;
width:100%;
margin-top: 50px;
text-align:center;
cursor:pointer;
&:hover{
    color: rgba(255,255,255,0.8);
}
`;



class loginPage extends React.Component {
    state={
        isFocused: false,
        loading: false,
    }
    onFocus = () => {
        this.setState({isFocused: true})
    }
    onBlur = () => {
        this.setState({isFocused: false})
    }
    handleClick = () => {
        this.props.onGetToken(this.state.username,this.state.password)
        this.setState({loading: true})
    };

    componentWillReceiveProps(nextProps){
        if(nextProps.successGetToken){
                this.setState({success: true})
        }
    }

    render() {
        if(this.state.success){
            return <Redirect to="/recipe-review" />
        } else{
        return (
            <ContainerPage>     
            <BackgroundPage isFocused = {this.state.isFocused}/>
                <LoginPanel isFocused = {this.state.isFocused}>
                    <LoginPanelBg>
                    </LoginPanelBg>
                    <ContentLogin>
                        <LogoContainer>
                            <HellofreshLogo src={hellofreshLogoSVG} />
                        </LogoContainer>
                        <InputContainer>
                        <InputHolder>
                        <Icon src={user}/>
                            <Input
                                placeholder="Username"
                                onFocus={this.onFocus}
                                onBlur={this.onBlur}
                                value={this.state.username}
                            />
                        </InputHolder>
                        <InputHolder>
                        <Icon src={lock}/>
                            <Input
                            placeholder="Password"
                                onFocus={this.onFocus}
                                onBlur={this.onBlur}
                                value={this.state.password}
                                />
                        </InputHolder>
                        
                            
                            <Btn
                            onClick={this.handleClick}
                            >LOGIN</Btn>
                            <Link href="#">Forget Password</Link>
                        </InputContainer>
                    </ContentLogin>
                </LoginPanel>
                <SidePanel isFocused = {this.state.isFocused}>
                    <Title>Say Hello to Easy Home Cooking</Title>
                    <Content>
                    HelloFresh SE is an international publicly traded meal-kit company based in Berlin, Germany. It is the second-largest meal-kit provider in the United States, after Blue Apron, and also has operations in Canada, Western Europe, and Australia.
                    </Content>
                            <HellofreshLogoMonoCrome src={hellofreshLogoMonoCromeSVG} />
                </SidePanel>
                { this.state.loading ? <Loader loading = {this.state.loading}/> : null}
            </ContainerPage>
        )
    }
    }
}

  export default loginPage


