import React from 'react'
import PropTypes from 'prop-types'
import styled, {css} from 'styled-components'
import cross from './../../assets/icon/Linearicons/cross.svg'

const Container = styled.div`
position:fixed;
top:0;
left:0;
width:100%;
height:100%;
display:none;
background-color: rgba(255,255,255,0.5);
${props => props.show && css`
display:flex;
z-index:100;
`}
@keyframes animat2 {
  from {
    opacity:0;
  }
  to {
    opacity:1;
  }
}

-webkit-animation: animat2 300ms ease-out ;
  -moz-animation: animat2 300ms ease-out ;
  -ms-animation: animat2 300ms ease-out ;
  -o-animation: animat2 300ms ease-out ;
  animation: animat2 300ms ease-out ;

  -webkit-animation-fill-mode: forwards;
`;
const Popup = styled.div`
position: relative;
height:80%;
width:30%;
@media (max-width: 900px) {
    ${ css`  
    width:90%;
      ` }
    }
margin:auto;
overflow: auto;
box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
background-color:white;
border-bottom: 5px solid green;
overflow: hidden;
@keyframes animat {
    from {
        transform: translate(0, 50px);
    }
    to {
        transform: translate(0, 0);
    }
}


animation: animat 500ms ease ;
animation-fill-mode: forwards;
`;

const HeaderOfContent = styled.div`
background: linear-gradient(to bottom, rgba(255,255,255,1) 60%,rgba(255,255,255,0) 100%);
position:absolute;
top:0;
height: 110px;
width: 100%;
right: 18px;
`;

const CloseBTN = styled.div`
position:absolute;
top:10px;
left:10px;
width:50px;
height:50px;
cursor:pointer;
background-color: rgba(0,0,0,0.1);
transition: all 0.3s ease;
background-repeat:no-repeat;
background-size:cover;
${props => props.icon && css`
background-image: url(${props.icon});
`}
&:hover{
    background-color: rgba(0,0,0,0.2);
}
`;

const List = styled.div`
padding-top:100px;
height:100%;
overflow: auto;
`;
const Item = styled.div`
padding:20px;
border-bottom: 1px solid rgba(0,0,0,0.1);
transition: all 0.2s ease;
cursor:pointer;
&:hover{
    background-color: rgba(0,0,0,0.2) 
}

`;

class CuisinesSelector extends  React.Component {
    state={
        show: false
    }
    componentWillReceiveProps(nextProps) {
        this.setState({ show: nextProps.show })
    }
    handleClickClose = () => {
        this.setState({ show: false })
    }
    handleClick = event => {
        this.props.selected(event.target.id)
    }
    render(){
        const {value } = this.props
        return(
            <div>
                <Container show={this.state.show}>
                <Popup>
                <HeaderOfContent/>
                <CloseBTN icon={cross} onClick={this.handleClickClose}></CloseBTN>
                <List>
                {value.map((item, id) =>
                                        <Item key={'item-'+id} id= {item.type} onClick={this.handleClick}>
                                            {item.name}
                                        </Item>
                            )}
                    </List>
                    </Popup>
                    </Container>
            </div>
        )
    }
}
CuisinesSelector.propTypes = {
    value: PropTypes.array.isRequired,
}

CuisinesSelector.defaultProps = {
    value: []
}

export default CuisinesSelector


