import * as types from '../constants/ActionTypes';

const url ="https://gw.hellofresh.com/auth/token";
const headers = new Headers();
const method = "POST"
headers.append("Content-Type", "text/plain");
const body = JSON.stringify({ 
  "client_id": "hellofresh-dev-test",
 "client_secret": "g4c25EzG4#%Afeh07Bb#anbH5BQQ67bJ7!G6QZOA",
  "grant_type": "client_credentials",
   "scope": "public"
   })
const options = {
  method,
  headers,
  body,
};

export const getTokenRequest = () => {
  return {
    type: types.GET_TOKEN_REQUEST
  };
}

export const getTokenReceive = (json) => {
  return {
    type: types.GET_TOKEN_RECEIVE,
    gets: json,
    receivedAt: Date.now()
  }
}

const fetchGetToken = () => dispatch => {
  dispatch(getTokenRequest())
  return fetch(url, options)
    .then(response =>  response.json())
    .then(json => dispatch(getTokenReceive(json)))
}

function shoudeFetchGetToken(state){
  // return true
  const gets = state.getToken.lastUpdated
  if (!gets) {
    return true
  } else if (gets.isFetching) {
    return false
  } else {
    return gets.didInvalidate
  }
}

export const fetchTokenIfNeeded = () => (dispatch, getState) => {
  if (shoudeFetchGetToken(getState())) {
    return dispatch(fetchGetToken())
  }
}



