import React from 'react'
import PropTypes from 'prop-types'
import styled, {css} from 'styled-components'
import CuisinesSelector from './../cuisinesSelector'

const Container = styled.div`
`;

const BTN = styled.div`
padding: 12px;
border-bottom: 1px solid rgba(0,0,0,0.1);
transition: all 0.2s ease;
transition: all 0.2s ease;
cursor: pointer;
float: left;
align-self: end;
color: green;
background: white;
margin: 11px;
margin-left: 0;
&:hover{
    background-color: rgba(255,255,255,0.5) 
}
`;
const Selected = styled.div`
color: rgb(145,200,79);
padding: 13px;
font-size: 48px;
margin:10px;
background-color:#ffffffb3;
float:left;
box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
@media (max-width: 900px) {
    ${ css`  
    padding: 10px;
    font-size: 18px;
      ` }
    }
    @media (max-width: 400px) {
    ${ css`  
    display:none
      ` }
    }
`;

class Cuisines extends  React.Component {
    state = {
        show: false,
        selected: 'italian'
    }
    handleClick = () => {
        this.setState({ show: true })
    }
    render(){
        const {value } = this.props
        const {selected } = this.state
        return(
            <div>
                <Container>
                <Selected> {selected.charAt(0).toUpperCase() + selected.slice(1) + ' Foods'} </Selected>
                <BTN onClick={this.handleClick}> Change Cuisines </BTN>
                <CuisinesSelector 
                show={this.state.show} 
                value={value}
                selected = {(data)=>{this.props.selected(data) 
                this.setState({selected: data})
                this.setState({show: false})
                }}
                />
                    </Container>
            </div>
        )
    }
}
Cuisines.propTypes = {
    value: PropTypes.array.isRequired,
}

Cuisines.defaultProps = {
    value: []
}

export default Cuisines


