import React from "react"
import PropTypes from 'prop-types'
import styled, { css } from 'styled-components'
import Recipe from './../recipe'
import Loader from './../smalLoader'
import heart from './../../assets/icon/Linearicons/heart.svg'
import heartFill from './../../assets/icon/Linearicons/heart-fill.svg'
import download from './../../assets/icon/Linearicons/download.svg'
import link from './../../assets/icon/Linearicons/link.svg'
import Rate from './../rate'


const Container = styled.div`
position: relative;
width: 340px;
height:500px;
margin:16px;
@media (max-width: 900px) {
    ${ css`  
    height:300px;
      ` }
    }
`;
const Card = styled.div`
position:relative;
background-color:white;
width: 350px;
height:100%;
overflow:hidden;
transition: all 0.2s ease;
box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
opacity: 0;
transition: all 0.5s ease;
transform: translate(0px, 50px);
&:hover{
    box-shadow: 0 19px 38px rgba(0,0,0,0.30), 0 15px 12px rgba(0,0,0,0.22);
}
${props => props.show && css`
opacity: 1;
transform: translate(0, 0);
`}
`;

const Image = styled.img`
width:100%;
height:240px;
background-color:rgba(0,0,0,0.3);
border-bottom: 5px solid green;
@keyframes mainImage {
  from {
    opacity:0;
  }
  to {
    opacity:1;
  }
}
animation: mainImage 5s ease ;
animation-fill-mode: forwards;
`;
const FirstContainer = styled.div`
position:relative;
cursor: pointer;
`;

const SecendContainer = styled.div`
padding:10px;
cursor: pointer;
`;

const TitleOfCard = styled.h1`
font-size:20px;
background-color:white;
padding: 10px 20px;
color: rgb(98,161,69);
max-width: 250px;
position:absolute;
bottom:10px;
border-left: 5px solid;
`;
const Text = styled.div`
color: gray;
font-size:17px;
max-height:170px;
overflow: hidden;
`;

const Headline = styled.div`
color: rgb(145,200,79);
font-weight:bold;
font-size: 20px;
`;

const FooterCard = styled.div`
width:100%;
position:absolute;
bottom: 0;
height: 115px;
background: linear-gradient(to bottom, rgba(255,255,255,0) 0%,rgba(255,255,255,1) 54%);
display:flex;
align-items:flex-end;
direction: rtl;
padding:5px;
`;

const BTNIcon = styled.a`
width: 35px;
height: 35px;
border-radius: 50%;
background-repeat: no-repeat;
background-size: 60%;
background-position: 7px 6px;
transition: all 0.5s ease;
cursor:pointer;
margin:5px;
border: 1px solid rgba(145,200,79,1);
${props => props.icon && css`
background-image: url(${props.icon});
`}
${props => props.positive && css`
background-color: rgb(145,200,79);
`}
&:hover{
box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
${props => props.hoverIcon && css`
background-image: url(${props.hoverIcon});
`}
}
`;

const RateRecipe = styled.div`
position:absolute;
bottom: 0;
left:0;
`;

const resizer = (valueOfStrings, x, y) => {
    let arrayOfStrings = valueOfStrings.split('0,0')
    return arrayOfStrings.join(x + ',' + y)
}

class RecipeListElement extends React.Component {
    state = {
        show: false,
        imageStatus: this.props.show,
    }
    handleClickOpenPopup = () => {
        this.setState({ show: true })
    }
    handleImageLoaded = () => {
        this.setState({ imageStatus: "loaded" });
    }

    handleClickFavorite = () => {
        if(this.state.favorite){
            this.setState({favorite : false})
        } else{
            this.setState({favorite : true})
        }
    }
    handleClickRank = () => {
        if(this.state.rank){
            this.setState({rank : false})
        } else{
            this.setState({rank : true})
        }
    }
    render() {
        const { value } = this.props
        return (
            <Container>
                <Loader/>
                <Card show={this.state.imageStatus === "loaded" ? true : false}>
                    <FirstContainer onClick={this.handleClickOpenPopup}>
                        <Image src={resizer( value.imageLink, 350,0)}
                            onLoad={this.handleImageLoaded}
                        />
                        <TitleOfCard>{value.name}</TitleOfCard>
                    </FirstContainer>
                    <SecendContainer onClick={this.handleClickOpenPopup}>
                        <Headline>{value.headline}</Headline>
                        <br/>
                        <Text>{value.description}</Text>
                    </SecendContainer>
                    <FooterCard>
                            <BTNIcon onClick={this.handleClickFavorite} icon={ this.state.favorite ? heartFill : heart} hoverIcon={heartFill}/>
                            <BTNIcon href={value.cardLink} target="_blank" icon={download} right={246}/>
                            <BTNIcon href={value.websiteUrl} target="_blank" icon={link} right={313}/>

                    </FooterCard>
                    <RateRecipe>
                    <Rate rate={value.averageRating} averageRating={value.averageRating} small />
                    </RateRecipe>
                </Card>
                    <Recipe show={this.state.show} value={value} />
            </Container>
        )
    }
}

RecipeListElement.propTypes = {
    value: PropTypes.object.isRequired,
}

RecipeListElement.defaultProps = {
    value: {}
}

export default RecipeListElement


