import React, { Component } from 'react';
import { Provider } from 'react-redux';
import {
  BrowserRouter as Router,
  Route
} from 'react-router-dom'
import store from '../store';
import loginPage from './loginPage'
import RecepeReviewPage from './recepeReviewPage'
import Home from './../components/temp/home'

class App extends Component {
  render() {
    return (
      <div className="App">
        <Provider store={store}>
          <Router>
            <div>
              <Route exact path="/" component={Home} />
              <Route path="/test" component={loginPage} />
              <Route path="/login" component={loginPage} />
              <Route path="/test/login" component={loginPage} />
              <Route path="/recipe-review" component={RecepeReviewPage} />
              <Route path="/test/recipe-review" component={RecepeReviewPage} />
            </div>
          </Router>
        </Provider>
      </div>
    );
  }
}

export default App;



