import React from 'react'
import PropTypes from 'prop-types'
import styled, {css} from 'styled-components'
import star from './../../assets/icon/Linearicons/star.svg'
import starFill from './../../assets/icon/Linearicons/star-fill.svg'

const BTNIconRate = styled.a`
width:30px;
height:30px;
${props => props.small && css`
width:20px;
height:20px;
`}
background-repeat:no-repeat;
background-size:100%;
background-position: 0px 0px;
transition: all 0.5s ease;
cursor:pointer;
${props => props.icon && css`
background-image: url(${props.icon});
`}
${props => props.positive && css`
background-color: rgb(145,200,79);
`}
`;

const AverageRating = styled.div`
font-size: 12px;
color: rgba(145,200,79,1);
padding-left: 10px;
`;

const Container = styled.div`
display:flex;
padding: 8px;
margin: 12px;
transition: all 0.5s ease;
&:hover{
box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
}
}
`;

class Rate extends React.Component {

    state={
        rate:this.props.rate,
        rateShow:this.props.rate
    }

    handleClickRate = event => {
        this.setState({ rateShow: event.target.id })
        this.setState({ rate: event.target.id })
    }

    onMouseEnterHandlerRate = event => {
        this.setState({ rateShow: event.target.id })
    }
    onMouseLeaveHandlerRate = event => {
        this.setState({ rateShow: this.state.rate})
    }

    render(){
        const {averageRating, small } = this.props
        return(<Container>
            <BTNIconRate id="1"
                onClick={this.handleClickRate}
                onMouseEnter={this.onMouseEnterHandlerRate}
                onMouseLeave={this.onMouseLeaveHandlerRate}
                icon={this.state.rateShow >= 1 ? starFill : star}
                small={small}
                />
            <BTNIconRate id="2"
                onClick={this.handleClickRate}
                onMouseEnter={this.onMouseEnterHandlerRate}
                onMouseLeave={this.onMouseLeaveHandlerRate}
                icon={this.state.rateShow >= 2 ? starFill : star}
                small={small}
                />
            <BTNIconRate id="3"
                onClick={this.handleClickRate}
                onMouseEnter={this.onMouseEnterHandlerRate}
                onMouseLeave={this.onMouseLeaveHandlerRate}
                icon={this.state.rateShow >= 3 ? starFill : star}
                small={small}
                />
            <BTNIconRate id="4"
                onClick={this.handleClickRate}
                onMouseEnter={this.onMouseEnterHandlerRate}
                onMouseLeave={this.onMouseLeaveHandlerRate}
                icon={this.state.rateShow >= 4 ? starFill : star}
                small={small}
                />
                 <AverageRating>
                  {averageRating}
                 </AverageRating>
        </Container>)
    }
}

Rate.propTypes = {
    rate: PropTypes.number,
    averageRating: PropTypes.number,
    small: PropTypes.bool,
}

Rate.defaultProps = {
    rate: 0,
    averageRating:0,
    small:false

}
export default Rate


