import React from "react"
import PropTypes from 'prop-types'
import styled, { css } from 'styled-components'
import hellofreshLogoMonoCoromSVG from './../../assets/logo/hellofresh-logo.svg'
import cross from './../../assets/icon/Linearicons/cross.svg'
import picture from './../../assets/icon/Linearicons/picture.svg'
import heart from './../../assets/icon/Linearicons/heart.svg'
import heartFill from './../../assets/icon/Linearicons/heart-fill.svg'
import download from './../../assets/icon/Linearicons/download.svg'
import link from './../../assets/icon/Linearicons/link.svg'
import Loader from './../smalLoader'
import Rate from './../rate'
import PopUp from './../popUp'

const Image = styled.div`
position:absolute;
background-color: rgb(98,161,69);
transition: all 0.5s ease;
background-repeat:no-repeat;
background-size: ${window.innerWidth - 200 + 'px ' + window.innerHeight - 500 + 'px'};
height:100%;
width:35%;
@media (max-width: 900px) {
    ${ css`  
    width:0%;
      ` }
    }
box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
${props => props.open && css`
width: 70%;
`}
transition: all 0.5s ease;
opacity: 0;
transform: translate(50px, 0);
${props => props.show && css`
opacity: 1;
transform: translate(0, 0);
overflow: hidden;
`}
`;

const ImageLoading = styled.div`
position:absolute;
top:0;
left:0;
background-color: rgba(34, 86, 10, 0.28);
height:100%;
width:35%;
@media (max-width: 900px) {
    ${ css`  
    display:none;
      ` }
    }
`;

const Img = styled.img`
height:100%;
`;
const CloseBTN = styled.div`
position:absolute;
top:10px;
left:10px;
width:50px;
height:50px;
cursor:pointer;
background-color: rgba(0,0,0,0.5);
transition: all 0.3s ease;
background-repeat:no-repeat;
background-size:cover;
${props => props.icon && css`
background-image: url(${props.icon});
`}
&:hover{
    background-color: rgba(0,0,0,0.8);
}
`;
const TitleOfcard = styled.div`
color: rgba(98, 161, 69);
border: 4px solid;
padding: 3px 20px;
position: absolute;
top: 100px;
left: 100px;
font-size: 47px;
background: white;
box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
@media (max-width: 900px) {
    ${ css`  
    left: 10px;
    top: 110px;
    font-size:20px;
      ` }
    }
`;
const Headline = styled.div`
background: rgb(98,161,69);
border: 4px solid;
padding: 10px 20px;
position: absolute;
top: 177px;
left: 100px;
font-size: 26px;
color: white;
box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
@media (max-width: 900px) {
    ${ css`  
    display:none
      ` }
    }
`;

const Content = styled.div`
position: absolute;
display:flex;
top: 0;
width: 65%;
height: 100%;
padding-top:250px;
left: 35%;
overflow: auto;
flex-direction: row;
flex-wrap: wrap;
@media (max-width: 900px) {
    ${ css`  
    padding-top:180px;
    width: 100%;
    left:0;
      ` }
    }
`;
const HeaderOfContent = styled.div`
background: linear-gradient(to bottom, rgba(255,255,255,1) 60%,rgba(255,255,255,0) 100%);
position:absolute;
top:0;
height: 250px;
width: 65%;
right: 18px;
@media (max-width: 900px) {
    ${ css`  
    width: 100%;
      ` }
    }
`;


const Text = styled.div`
font-size:20px;
padding: 0 41px;
color: gray;
`;

const Title = styled.div`
font-size:30px;
padding:20px;
color: green;
`;

const Text2 = styled.span`
font-size:17px;
color: rgba(255,255,255,0.8);
float:right
`;

const Title2 = styled.span`
font-size:17px;
font-weight:bold;
color: rgba(255,255,255,1);
`;

const Logo = styled.img`
width: 100px;
position: absolute;
right: 13px;
top: 29px;
`;

const NutritionalValue = styled.div`
background-color:rgb(145, 200, 79);
position:absolute;
bottom: 10px;
left:10px;
padding:20px;
width:260px;
box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
@media (max-width: 900px) {
    ${ css`  
    display:none
      ` }
    }
`;

const Ingredients = styled.div`
background-color:rgba(0, 0, 0,0.0);
margin:53px;
margin-top:5px;
box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
`;

const IngredientsCard = styled.div`
background-color:rgba(0, 0, 0,0.0);
margin:25px;
margin-top:5px;
width:200px;
height:100px;
display:flex;
box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
`;

const IngredientsPicture = styled.div`
${props => props.image && css`
background-image: url(${props.image});
background-size: 155%;
`}
height:100px;
width:100px;
align-self: flex-end;
`;

const TextIngredients = styled.div`
font-size:20px;
padding:10px;
width:60%;
`;

const Row = styled.div`
width:100%;
`;

const ContainerWrap = styled.div`
display:flex;
flex-direction:row;
flex-wrap:wrap;
`;

const FabBTN = styled.div`
width:80px;
height:80px;
border-radius:50%;
background-color: rgb(145,200,79);
position:absolute;
left: 32%;
bottom: 40px;
opacity: 0;
background-repeat:no-repeat;
background-size:45%;
background-position: 21px 22px;
${props => props.icon && css`
background-image: url(${props.icon});
`}
${props => props.open && css`
left: 67%;
`}
${props => props.show && css`
opacity: 1;
`}
transition: all 0.5s ease;
cursor:pointer;
box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
&:hover{
    background-color: rgba(145,200,79,0.9);
}
@media (max-width: 900px) {
      display:none;
`;

const StepNum = styled.div`
font-size:20px;
width:30px;
height:30px;
color:white;
text-align:center;
border-radius:50%;
background:rgb(98,161,69);
line-height: 30px;
`;
const StepNumContainer = styled.div`
height:0;
widows:0;
overflow:visible;
margin: -16px;
`;

const StepPicture = styled.img`
width:300px;
@media (max-width: 900px) {
    ${ css`  
    display:none
      ` }
    }
`;

const WinePicture = styled.img`
height:200px;
`;

const StepContent = styled.div`
display:flex;
`;

const StepText = styled.div`
padding:20px;
font-size:17px;
line-height:32px;
color: rgba(100,100,100,1);
`;

const BTNIcon = styled.a`
width:50px;
height:50px;
border-radius:50%;
margin:10px;
margin-right:0px;
margin-left:5px;
background-repeat:no-repeat;
background-size:50%;
background-position: 12px 12px;
transition: all 0.5s ease;
cursor:pointer;
border: 1px solid rgba(145,200,79,1);
${props => props.icon && css`
background-image: url(${props.icon});
`}
${props => props.positive && css`
background-color: rgb(145,200,79);
`}
&:hover{
box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
${props => props.hoverIcon && css`
background-image: url(${props.hoverIcon});
`}
}
`;

const HeaderIcon = styled.div`
display:flex;
position:absolute;
right:120px;
top:40px;
`;

const TagContainer = styled.div`
display:flex;
padding: 10px 20px;
width:100px;
position: absolute;
top: 250px;
left: 100px;
flex-direction: row;
flex-wrap: wrap;
width: 500px;
padding-left: 0;
@media (max-width: 900px) {
    ${ css`  
    display:none
      ` }
    }
`;
const Tag = styled.div`
background: rgb(98,161,69);
box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
color: white;
font-size: 16px;
margin: 10px;
padding: 10px;
    padding-left: 10px;
padding-left: 45px;
background-size: 29px;
background-repeat: no-repeat;
background-position-x: 5px;
background-position-y: 5px;
width: -moz-fit-content;
min-width: 129px;
${props => props.icon && css`
background-image: url(${props.icon});
`}
`;
const Tag2 = styled.span`
font-size:17px;
font-weight:bold;
color: rgba(255,255,255,1);
background: rgb(209, 5, 55);
padding:10px 20px;
`;

const resizer = (valueOfStrings, x, y) => {
    let arrayOfStrings = valueOfStrings.split('0,0')
    return arrayOfStrings.join(x + ',' + y)
}

class recipe extends React.Component {
    state = {
        show: this.props.show,
        imageStatus: false,
        openPicture: false,
        rate: 3
    }

    componentWillReceiveProps(nextProps) {
        this.setState({ show: nextProps.show })
    }
    handleClick = () => {
        this.setState({ show: false })
    }
    handleClickfabBTN = () => {
        if (this.state.openPicture) {
            this.setState({ openPicture: false })
        } else {
            this.setState({ openPicture: true })
        }
    }
    handleImageLoaded = () => {
        this.setState({ imageStatus: "loaded" });
    }

    handleClickFavorite = () => {
        if (this.state.favorite) {
            this.setState({ favorite: false })
        } else {
            this.setState({ favorite: true })
        }
    }

    render() {
        const { value } = this.props
        return (
                <PopUp show={this.state.show}>
                    <Content>
                        <Row>
                            <Title>Description: </Title>
                            <Text>{value.description}</Text>
                        </Row>
                        <Row>
                            <Title>Ingredients: </Title>
                            <ContainerWrap>
                                {value.ingredients.filter((item) => item.shipped === true).map((item, id) =>
                                    <IngredientsCard key={'ingredient'+id}>
                                        <TextIngredients>{item.name}</TextIngredients>
                                        <IngredientsPicture image={item.imageLink} />
                                    </IngredientsCard>
                                )}

                            </ContainerWrap>
                            <Title>Ingredients Not included in your delivery: </Title>
                            <ContainerWrap>
                                {value.ingredients.filter((item) => item.shipped !== true).map((item, id) =>
                                    <IngredientsCard key={'ingredients'+id}>
                                        <TextIngredients>{item.name}</TextIngredients>
                                        <IngredientsPicture image={item.imageLink} />
                                    </IngredientsCard>
                                )}

                            </ContainerWrap>
                        </Row>
                        <Row>
                            <Title>Allergens: </Title>
                            <ContainerWrap>
                                <Text>
                                    {value.allergens.map((item, id) =>
                                        <Tag2 key={'allerge'+id}>{item.name}</Tag2>
                                    )}

                                </Text>
                                <br />
                            </ContainerWrap>
                        </Row>
                        <Row>
                            <Title>Steps: </Title>

                            {value.steps.map((item, id) =>
                                <Ingredients key={'step'+id}>
                                    <StepContent>
                                        <StepContent>
                                            <StepPicture src={resizer(item.images.length ? item.images[0].link ? item.images[0].link : '' : '', 300)} />
                                            <StepNumContainer>
                                                <StepNum>{item.index}</StepNum>
                                            </StepNumContainer>
                                        </StepContent>
                                        <StepContent>
                                            <StepText> {item.instructions} </StepText>
                                        </StepContent>
                                    </StepContent>
                                </Ingredients>
                            )}
                            <Title>Wines: </Title>

                            <Ingredients>
                                <StepContent>
                                    {value.wines.map((item, id) =>

                                        <WinePicture key={'wine'+id} src={'https://partner.wineassets.net/' + item + '_bottle.png'} />

                                    )}
                                </StepContent>
                            </Ingredients>
                            <br />
                        </Row>
                    </Content>
                    <HeaderOfContent>
                        <Logo src={hellofreshLogoMonoCoromSVG} />
                        <HeaderIcon>
                        <Rate  averageRating={value.averageRating} rate={value.averageRating}/>
                            <BTNIcon onClick={this.handleClickFavorite} icon={this.state.favorite ? heartFill : heart} hoverIcon={heartFill} />
                            <BTNIcon href={value.cardLink} target="_blank" icon={download} />
                            <BTNIcon href={value.websiteUrl} target="_blank" icon={link} />
                        </HeaderIcon>

                    </HeaderOfContent>
                    <ImageLoading>
                        <Loader />
                    </ImageLoading>
                    <Image image={resizer(value.imageLink, 0, (window.innerHeight - 400))} show={this.state.imageStatus === "loaded" ? true : false} open={this.state.openPicture}>
                        <Img src={resizer(value.imageLink, 0, (window.innerHeight - 400))}
                            onLoad={this.handleImageLoaded}
                        />
                    </Image>
                    <FabBTN show={this.state.imageStatus === "loaded" ? true : false} icon={picture} onClick={this.handleClickfabBTN} open={this.state.openPicture}></FabBTN>
                    <TagContainer>
                        {value.tags.map((item, id) =>
                            <Tag key={'tag'+id} icon={item.iconLink}>
                                {item.name}
                            </Tag>
                        )}
                    </TagContainer>
                    <Headline>{value.headline} </Headline>
                    <TitleOfcard>{value.name}</TitleOfcard>
                    <CloseBTN icon={cross} onClick={this.handleClick}></CloseBTN>
                    <NutritionalValue>
                        <Title2>Nutrition Values  </Title2><Text2> /per serving</Text2>
                        <br />
                        <br />
                        {value.nutrition.map((item, id) =>
                            <div key={'nutrition'+id}>
                                <Title2>{item.name}: </Title2> <Text2> {item.amount} {item.unit}</Text2>
                            </div>
                        )}
                    </NutritionalValue>
                </PopUp>
        )
    }
}

recipe.propTypes = {
    value: PropTypes.object.isRequired,
}

recipe.defaultProps = {
    value: {},
}

export default recipe


